import * as React from 'react';
import PropTypes from 'prop-types';
import { styled } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Check from '@mui/icons-material/Check';
import StepConnector, { stepConnectorClasses } from '@mui/material/StepConnector';
import { CardMedia, Grid, StepContent, Typography, Box } from '@mui/material';
import OurghemaIcon  from '../../Media/ourghema1.png';
import EdgerydersIcon from '../../Media/Edgeryders.png'
import LightencyIcon from '../../Media/Lightency.png'
import UIBIcon from '../../Media/uib.png'
import MedialogIcon from '../../Media/Medialog.png'
import MedialogImage from '../../Media/MedialogImage.jpg'
import OurghemaImage  from '../../Media/OurghemaImage.jpg';
import EdgerydersImage from '../../Media/EdgerydersImage.jpg'
import LightencyImage from '../../Media/LightencyImage.jpg'
import UIBImage from '../../Media/UIBImage.jpg'
import Description from './Description';

const QontoConnector = styled(StepConnector)(({ theme }) => ({
  [`&.${stepConnectorClasses.alternativeLabel}`]: {
    top: 10,
    left: 'calc(-50% + 16px)',
    right: 'calc(50% + 16px)',
  },
  [`&.${stepConnectorClasses.active}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      borderColor: '#784af4',
    },
  },
  [`&.${stepConnectorClasses.completed}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      borderColor: '#784af4',
    },
  },
  [`& .${stepConnectorClasses.line}`]: {
    borderColor: theme.palette.mode === 'dark' ? theme.palette.grey[800] : '#eaeaf0',
    borderTopWidth: 3,
    borderRadius: 1,
  },
}));

const StyledStep = styled(Step)({
        cursor:"pointer"
  });
const QontoStepIconRoot = styled('div')(({ theme, ownerState }) => ({
  color: theme.palette.mode === 'dark' ? theme.palette.grey[700] : '#eaeaf0',
  display: 'flex',
  height: 22,
  alignItems: 'center',
  ...(ownerState.active && {
    color: '#784af4',
  }),
  '& .QontoStepIcon-completedIcon': {
    color: '#784af4',
    zIndex: 1,
    fontSize: 18,
  },
  '& .QontoStepIcon-circle': {
    width: 8,
    height: 8,
    borderRadius: '50%',
    backgroundColor: 'currentColor',
  },
}));

function QontoStepIcon(props) {
  const { active, completed, className } = props;

  return (
    <QontoStepIconRoot ownerState={{ active }} className={className}>
      {completed ? (
        <Check className="QontoStepIcon-completedIcon" />
      ) : (
        <div className="QontoStepIcon-circle" />
      )}
    </QontoStepIconRoot>
  );
}

QontoStepIcon.propTypes = {
  /**
   * Whether this step is active.
   * @default false
   */
  active: PropTypes.bool,
  className: PropTypes.string,
  /**
   * Mark the step as completed. Is passed to child components.
   * @default false
   */
  completed: PropTypes.bool,
};

const ColorlibConnector = styled(StepConnector)(({ theme }) => ({
  [`&.${stepConnectorClasses.alternativeLabel}`]: {
    top: 22,
  },
  [`&.${stepConnectorClasses.active}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      backgroundImage:
        'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
    },
  },
  [`&.${stepConnectorClasses.completed}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      backgroundImage:
        'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
    },
  },
  [`& .${stepConnectorClasses.line}`]: {
    height: 30,
    width: 5,
    margin:'0 0px 0 10px',
    border: 0,
    backgroundColor:
      theme.palette.mode === 'dark' ? theme.palette.grey[800] : '#eaeaf0',
    borderRadius: 1,
  },
}));

const ColorlibStepIconRoot = styled('div')(({ theme, ownerState }) => ({
  backgroundColor: "tan",
  zIndex: 1,
  color: '#fff',
  width: 50,
  height: 50,
  display: 'flex',
  cursor:"pointer",
  borderRadius: '50%',
  justifyContent: 'center',
  alignItems: 'center',
  ...(ownerState.active && {
    backgroundImage:
    'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
      // 'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(124,92,100) 50%, rgb(0,117,120) 100%)',
    boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
  }),
  ...(ownerState.completed && {
    backgroundImage:
    'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
  }),
}));

function ColorlibStepIcon(props) {
  const { active, completed, className } = props;


const icons = {
  1: <img src={UIBIcon} width="32px" height="25px"  ></img>,
  2: <img src={MedialogIcon} width="30px" height="30px" ></img>,
  3: <img src={LightencyIcon} width="30px" height="30px" ></img>,
  4: <img src={EdgerydersIcon} width="30px" height="30px" ></img>,
  5: <img src={OurghemaIcon} width="30px" height="30px" ></img>,
  };
  return (
    <ColorlibStepIconRoot ownerState={{ completed, active }} className={className}>
      {icons[String(props.icon)]}
    </ColorlibStepIconRoot>
  );

}

ColorlibStepIcon.propTypes = {
  /**
   * Whether this step is active.
   * @default false
   */
  active: PropTypes.bool,
  className: PropTypes.string,
  /**
   * Mark the step as completed. Is passed to child components.
   * @default false
   */
  completed: PropTypes.bool,
  /**
   * The label displayed in the step icon.
   */
  icon: PropTypes.node,
};

const steps = ['UIB Bank', 'Med Dialogue', 'Lightency', 'Edgeryders (OCI Labs)'];

  function getStepContent(step) {
    switch (step) {
      case 0:
        return ["Working on the Robotic Process Automation (RPA) to process SWIFT messages MT103 in Full STP",
          "Working on Accounting Reconciliation of Nostri accounts",
          "Working on VBA to automate the accounting process",
          "Developing and Deploying Deep learning model"
          ];
      case 1:
        return ["Developing & Deploying platforms along with adequate back-end",
                "Providing rechnical support for online events",
                "Managing Databases of the online forms",
                "Increasing Traffic and websites rank using SEO Techniques"];
      case 2:
        return [
          "Developed a marketing strategy for the company",
          "Made international competitive market analysis",
          "Audit online promotion tools", 
            "Contributed to the development of the Business Plan",
              "Developed a marketing strategy for the company",
              "Made international competitive market analysis",
          ];
      case 3:
          return ["Developed online marketing strategy and implemented it",
                  "Prepared website content and managed SEO",
                  "Maintained good communication with the participants in the project"];
      case 4:
        return ["Managed social media accounts",
               "Created social media graphics for daily postings",
                "Developed Website using Wordpress",];
      default:
        return 'Unknown step';
    }
  }

  function getPoste(step) {
    switch (step) {
      case 0:
        return 'RPA DEVELOPMENT';
      case 1:
        return 'JUNIOR IT CONSULTANT (Freelance)';
      case 2:
        return 'GROWTH HACKER';
      case 3:
        return 'COMMUNITY BUILDER';
      case 4:
        return 'COMMUNITY MANAGER';
      default:
        return 'Unknown step';
    }
  }
  
  function getDate(step) {
    switch (step) {
      case 0:
        return 'Jul 2020 - Present';
      case 1:
        return 'Oct 2020 - Present';
      case 2:
        return 'Jul 2019 - Mar 2020';
      case 3:
        return 'Aug 2019 - Oct 2019';
      case 4:
        return 'Jun 2018 - Sep 2018';
      default:
        return 'Unknown step';
    }
  }
  function getStepImage(step) {
    switch (step) {
      case 0:
        return <>
        <CardMedia
          component="img"
          sx={{ width: "100%",maxHeight:"300px" }}
          image={UIBImage}
          alt="UIB Image"
        /></>;
      case 1:
        return <>
        <CardMedia
          component="img"
          sx={{ width: "100%",maxHeight:"300px" }}
            image={MedialogImage}
          alt="Med Dialogue for rights and equality image"
          /></>;
      case 2:
        return <>
        <CardMedia
          component="img"
          sx={{ width: "100%",maxHeight:"300px" }}
          image={LightencyImage}
          alt="Lightency Icon"
        /></>;
      case 3:
        return <>
        <CardMedia
          component="img"
          sx={{ width: "100%",maxHeight:"300px" }}
          image={EdgerydersImage}
          alt="Edgeryders Icon"
        /></>;
      case 4:
        return <>
        <CardMedia
          component="img"
          sx={{ width: "100%",maxHeight:"300px" }}
          image={OurghemaImage}
          alt="Ourghema Icon"
        /></>;
      default:
        return 'Unknown Icon';
    }
  }


export default function Experience() {
    const [activeStep, setActiveStep] = React.useState(0);

    const handleStep = (step) => () => {
      setActiveStep(step);
    };
  return (
    <div style={{backgroundColor:"rgb(46, 59, 85)",padding:'30px 0 30px 0'}} id="Experience">
    <Typography variant="h3" marked="center" align="center" component="h2" style={{color:"#fff"}}>
      Experience
    </Typography>
    <br></br>
    <Grid container  sx={{display: { xs: 'none', md: 'flex' }} }>
    <Grid item xs={12} md={3}>
    <Stack sx={{ width: '100%', marginLeft:15 }} spacing={4} >
      <Stepper  activeStep={activeStep} connector={<ColorlibConnector />} orientation="vertical">
        {steps.map((label,index) => (
          <StyledStep key={label} onClick={handleStep(index)}>
            <StepLabel StepIconComponent={ColorlibStepIcon}><Typography style={{color:"#fff",fontWeight:"500"}}>{label}</Typography></StepLabel>
          </StyledStep>
        ))}
      </Stepper>
    </Stack>
    </Grid>
    <Grid item xs={12} md={9}>
      {steps.map((label,index)=>(
            <>
            {index===activeStep ? <Description getStepContent={getStepContent} activeStep={activeStep} getStepImage={getStepImage} esm={label} getPoste={getPoste} getDate={getDate} />:"" }
            {/*  */}
            </>
      ))}
    </Grid>
    </Grid>
    
    <Stepper activeStep={activeStep} orientation="vertical" sx={{margin:5,display: { xs: 'flex', md: 'none' }} }>
        {steps.map((step, index) => (
          <Step key={step} onClick={handleStep(index)}>
            <StepLabel>    
              <Typography  sx={{color:"#fff", fontWeight:500, fontSize:18}}>{step}</Typography>
            </StepLabel>
            <StepContent>
                  <Typography component="div" variant="h5" style={{color:"#F2D492"}} >
                    {getPoste(activeStep)}
                  </Typography>
                  <Typography >
                    {getDate(activeStep)}
                  </Typography>
                  <br></br>
                  <Typography style={{color:"#F2D492", fontWeight:500, fontSize:13}}>
                    Responsibilities
                  </Typography>
                  <Typography variant="subtitle2" component="div" style={{marginLeft:5}} >
                    <ul>
                    {getStepContent(activeStep).map((label) => (
                      <li>{label}</li>
                    ))}
                    </ul>
                  </Typography>
            </StepContent>
          </Step>
        ))}
      </Stepper>
    </div>
  );
}