import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from './Textfield';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import {Formik, Form} from "formik";
import * as Yup from "yup";
import emailjs from 'emailjs-com';

const INITIAL_FORM_STATE ={
    Name:'',
    email:'',
    FreeField:'',
};
const FORM_VALIDATION= Yup.object().shape({
    Name: Yup.string()
        .required('Required'),
    email: Yup.string()
        .email('Please Enter a Valid Email')
        .required('Required'),
    FreeField: Yup.string()
        .required('Required'),
    
})
function sendEmail(templateParams) {
    emailjs.send('service_gslmkc6', 'template_ekbq6hr', templateParams,'user_c7UQezbNY7UTEEz1dbhlA')
    .then(function(response) {
       console.log('SUCCESS!', response.status, response.text);
    }, function(error) {
       console.log('FAILED...', error);
    });
  }
export default function FormDialog({open,handleClose}) {

    const [maftouh, setMaftouh] = React.useState(false)
    
    const handleFath = () => {
        setMaftouh(true);
    };
  
    const handleSakr = () => {
        setMaftouh(false);
    };
  return (
    <div>=
      <Dialog open={open} onClose={handleClose} style={{padding:15}}>
        <Formik initialValues={{
                    ...INITIAL_FORM_STATE
                    }}
                    validationSchema={FORM_VALIDATION}
                    onSubmit={values =>{
                        sendEmail(values);
                        console.log(values);
                        handleClose();
                        handleFath();
                    }}
                >
                {({ 
                    setFieldValue,
                    setFieldTouched,
                    values,
                    errors,
                    touched,
                }) => (
                    <Form>
                        <DialogTitle>Contact Me</DialogTitle>
                        <DialogContent>
                        <DialogContentText>
                            Feel free to send an e-mail on belguithiyed@gmail.com<br></br>
                            Alternatively, you can also drop-in a mail here!<br></br>
                        </DialogContentText>
                                    <TextField
                                    name="Name"
                                    label="Name"
                                    />
                                    <TextField
                                    name="email"
                                    label="Email"
                                    />
                                    <TextField
                                    name="FreeField"
                                    label="Message"
                                    multiline
                                    rows={4}
                                    />
                        </DialogContent>
                        <DialogActions>
                        <Button onClick={handleClose}>Cancel</Button>
                        <Button type="submit">Send</Button>
                        </DialogActions>
                    </Form>
                )}
            </Formik>
      </Dialog>
        <Dialog open={maftouh} onClose={handleSakr} style={{padding:15}}>
            <DialogTitle>Thank you for your message</DialogTitle>
        </Dialog>
    </div>
  );
}
